@media screen {
  .select2 {
    width: 100% !important;
  }

  table > thead > tr > th,
  .table > tbody > tr > th,
  .table > tfoot > tr > th,
  .table > thead > tr > td,
  .table > tbody > tr > td,
  .table > tfoot > tr > td {
    padding-top: 3px;
    padding-bottom: 3px;
  }

  .btn-xs,
  .btn-group-xs > .btn {
    margin: 2px;
  }
}